import { Button } from "antd";
import React, { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux";
import { ChatActions, startMessagesListening } from "../../../../redux/chat-reducer";

import { AppState } from "../../../../redux/store-redux";
import c from "./Message.module.scss";
type MessagesAPIItemType = {
    userName: string
    message: string
    userId: number
    photo: string
}
type MessagesItemType = MessagesAPIItemType & {id: string}
const AmogusPhoto = 'data:image/jpeg;base64,'
type MessageType = {
    
}
const Message: React.FC<MessageType> = React.memo((props) => {
    const messages = useSelector((state:AppState)=> state.chat.messages)
    const AutoScrollRef = useRef<HTMLDivElement>(null)
    const [isAuthoScroll, setAuthoScroll] = useState<boolean>(true)
    useEffect(()=>{
        if(isAuthoScroll === true){
            AutoScrollRef.current?.scrollIntoView({behavior: 'smooth'})
        }
    }, [messages])
    const scrollHundler = (e: React.UIEvent<HTMLDivElement, UIEvent>)=>{
        const element = e.currentTarget;
        if (Math.abs((element.scrollHeight - element.scrollTop) - element.clientHeight) < 300){
            if(isAuthoScroll === false){
                setAuthoScroll(true)
            }
        }
        else{
            if(isAuthoScroll === true){
                setAuthoScroll(false)
            }
        }
    }
    return <div className={c.ScrollContainer} onScroll={scrollHundler}>
        {messages.map((item: MessagesItemType, index) => {
            // return <div className={c.message} key={item.id}>
            return <div className={c.message} key={index}>
                <div className={c.flexContainer}>
                    <img style={{ maxWidth: "80px" }} src={item.photo !== null ? item.photo : AmogusPhoto} />
                    <div>
                        <div className={c.userName}>{item.userName}</div>
                        <div className={c.userId}>{item.userId}</div>
                        <div className={c.messageText}>{item.message}</div>
                    </div>
                </div>
                <div className={c.line}></div>
            </div>
        })}
        <div ref={AutoScrollRef}></div>
    </div>
})
export default Message